<template>
    <div>
        <div class="loginNavFix">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">评价</p>
        </div>
        <div class="position">
            <div class="permission">
                <div class="perBox ml-30">
                    <div class="per-title l f26 c333">各班评价</div>
                    <div class="per-edit r">
                        <div class="look f26 cc6 l">查看</div>
                        <img class="per-img r mt-33 mr-30 ml-20" src="../../../assets/my/more.png" alt="">
                    </div>
                </div>
                <div class="perBox ml-30" @click="renKe">
                    <div class="per-title l f26 c333">任课班级评价</div>
                    <div class="per-edit r">
                        <div class="look f26 cc6 l">查看</div>
                        <img class="per-img r mt-33 mr-30 ml-20" src="../../../assets/my/more.png" alt="">
                    </div>
                </div>
            </div>
            <div class="canvasBox">
                <div id="myChart" :style="{width: '300px', height: '300px',margin:'1rem auto'}"></div>
                <!-- <canvas id="canvas" width="400" height="400"></canvas> -->
                <div class="types num1">{{number.num1}}</div>
                <div class="types num2">{{number.num2}}</div>
                <div class="types num3">{{number.num3}}</div>
                <div class="types num4">{{number.num4}}</div>
                <div class="types num5">{{number.num5}}</div>
                <div class="maskT"></div>
            </div>
            <div class="tuli">
                <div class="biaozhu l mt-46 pl-36">
                    <div class="qiu l ff732c ml-36"></div>
                    <p class="tuliP l f26 pl-10">{{label.label1}}</p>
                </div>
                <div class="biaozhu l mt-46">
                    <div class="qiu l f5a801 ml-36"></div>
                    <p class="tuliP l f26 ml-10">{{label.label2}}</p>
                </div>
                <div class="biaozhu l mt-46">
                    <div class="qiu l f00d4a9 ml-36"></div>
                    <p class="tuliP l f26 ml-10">{{label.label3}}</p>
                </div>
                <div class="biaozhu1 l mt-46">
                    <div class="qiu l f69acfa ml-140"></div>
                    <p class="tuliP l f26 ml-10">{{label.label4}}</p>
                </div>
                <div class="biaozhu1 l mt-46">
                    <div class="qiu l accf1b ml-36"></div>
                    <p class="tuliP l f26 ml-10">{{label.label5}}</p>

                </div>
            </div>
            <!-- <img class="tuli" src="../../../assets/index/tuli.jpg" alt=""> -->
        </div>
        <van-popup v-model="showPopup" class="popupModel"  position="right" >
            <div class="popups" style="width:100vw;height:100vh;">
                <div class="loginNavFix">
                    <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returnPopup">
                    <p class="title f30 cfff text-center">评价</p>
                </div>
                <div class="position">
                    <div class="item-for">

                        <div class="item-list ml-30" v-for="(item,index) in list" :key="index">
                            <div style="overflow:hidden;" @click="clickThis(index)">
                                <div class="item-title l f30 c333">{{item.classesName}}</div>
                                <img class="item-img r" src="../../../assets/my/more.png" alt="">
                            </div>
                            <div class="chooseClass" v-show="item.show">
                                <div class="item-list ml-30" v-for="(items,indexs) in item.crmCourCoursesList" :key="indexs"  @click="getClassesEval(items.id)">
                                    <div class="line l"></div>
                                    <div class="item-title l f30">{{items.name}}</div>
                                </div>
                            </div>
                        </div>
                        
                    </div>
                </div>
            </div>
        </van-popup>



    </div>
</template>
<script>
export default {
  name: 'HelloWorld',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            showPopup : false,
            option : {
                title: {
                    text: ''
                },
                tooltip: {},
                legend: {
                    data: ['预算分配（Allocated Budget）', '实际开销（Actual Spending）']
                },
                radar: {
                    // shape: 'circle',
                    name: {
                        textStyle: {
                            color: '#fff',
                            backgroundColor: '#999',
                            borderRadius: 3,
                            padding: [3, 5]
                    }
                },
                indicator: []
                },
                series: [{
                    name: '',
                    type: 'radar',
                    color: 'blue',
                    background:'red',
                    areaStyle:{
                        color: {
                            type: 'linear',
                            x: 0,
                            y: 0,
                            x2: 0,
                            y2: 1,
                            colorStops: [{
                                offset: 0, color: '#82cbff' // 0% 处的颜色
                            }, {
                                offset: 1, color: '#7b7ceb' // 100% 处的颜色
                            }],
                            globalCoord: false, // 缺省为 false
                            
                        },
                        opacity:1,
                        shadowColor: '#82cbff',
                        shadowBlur: 100
                    },
                    data : [
                        {
                            value : [],
                            name : ''
                        },
                    ]
                }]
            },
            number:{
                num1:0,
                num2:0,
                num3:0,
                num4:0,
                num5:0
            },
            label:{
                label1 : '',
                label2 : '',
                label3 : '',
                label4 : '',
                label5 : ''
            },
            list : []
        }
    },
    methods : {
        returns(){
            this.$router.push({path:'/indexs'});//返回上一层
        },
        turnToMy(){
            this.$router.push({path:'/my'})
        },
        turnToMessage(){
            this.$router.push({path:'/message'})
        },
        drawLine(){
            // 基于准备好的dom，初始化echarts实例
            let myChart = this.$echarts.init(document.getElementById('myChart'))
            // 绘制图表
            myChart.setOption(this.option);
        },
        renKe(){
            this.showPopup = true;
            // this.$router.push({path:'/changeclass'});//返回上一层
        },
        returnPopup(){
            this.showPopup = false;
        },
        clickThis(index){//切换张开和闭合
            this.list[index].show = !this.list[index].show;
        },
        createOptin(id){
            this.showPopup = false;

        },
        teacherstudent(){
            this.$http({url:this.$api.teacherstudent.getTeacherRecord},{schoolId:JSON.parse(localStorage.getItem('zhxy_user')).schoolId,teacherId:JSON.parse(localStorage.getItem('zhxy_user')).id}).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.showPopup = false;
                    // 数字
                    this.number.num1 = data.data[0].startNum;
                    this.number.num2 = data.data[1].startNum;
                    this.number.num3 = data.data[2].startNum;
                    this.number.num4 = data.data[3].startNum;
                    this.number.num5 = data.data[4].startNum;
                    // 名字
                    this.label.label1 = data.data[0].labelName;
                    this.label.label2 = data.data[1].labelName;
                    this.label.label3 = data.data[2].labelName;
                    this.label.label4 = data.data[3].labelName;
                    this.label.label5 = data.data[4].labelName;
                    // 雷达图
                    var arr = [];
                    for(var i in data.data){
                        arr.push(data.data[i].startNum);
                    }
                    this.option.series[0].data[0] = {
                        value : arr,
                        name : ''
                    }
                    var num = arr[0];
                    for(var i=0;i<arr.length;i++){
                        if(num < arr[i]){
                            num = arr[i]
                        }
                    }
                    var arr1 = [];
                    for(var i = 0;i < 5;i++){
                        arr1.push({name:'',max:num})
                    }
                    this.option.radar.indicator = arr1;
                    this.drawLine();
                }else{
                    this.$toast(data.msg);
                }
            },err => {
                this.$toast('出错了');
            })
        },
        getByTeacherId(){
            this.$http({url:this.$api.teacherstudent.getByTeacherId},{teacherId:JSON.parse(localStorage.getItem('zhxy_user')).id}).then(data => {
                console.log(data);
                if(data.code == 0){
                    for(var i in data.data){
                        data.data[i].show = false;
                    }
                    this.list = data.data;
                }else{
                    this.$toast(data.msg);
                }
            })
        },
        getClassesEval(id){//任课班级评价
            var param = {
                schoolId : JSON.parse(localStorage.getItem('zhxy_user')).schoolId,
                teacherId : JSON.parse(localStorage.getItem('zhxy_user')).id,
                classesId : JSON.parse(localStorage.getItem('zhxy_user')).classesId,
                coursesId : id
            };
            this.$http({url:this.$api.teacherstudent.getClassesEval},param).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.showPopup = false;
                    // 数字
                    this.number.num1 = data.data[0].labelCount;
                    this.number.num2 = data.data[1].labelCount;
                    this.number.num3 = data.data[2].labelCount;
                    this.number.num4 = data.data[3].labelCount;
                    this.number.num5 = data.data[4].labelCount;
                    // 名字
                    this.label.label1 = data.data[0].label_name;
                    this.label.label2 = data.data[1].label_name;
                    this.label.label3 = data.data[2].label_name;
                    this.label.label4 = data.data[3].label_name;
                    this.label.label5 = data.data[4].label_name;
                    // 雷达图
                    var arr = [];
                    for(var i in data.data){
                        arr.push(data.data[i].labelCount);
                    }
                    this.option.series[0].data[0] = {
                        value : arr,
                        name : ''
                    }
                    var num = arr[0];
                    for(var i=0;i<arr.length;i++){
                        if(num < arr[i]){
                            num = arr[i]
                        }
                    }
                    var arr1 = [];
                    for(var i = 0;i < 5;i++){
                        arr1.push({name:'',max:num})
                    }
                    this.option.radar.indicator = arr1;
                    this.drawLine();
                }else{
                    this.$toast(data.msg);
                }
            })
        }
        
    },
    created() {
        
    },  
    mounted() {
        this.teacherstudent();
        this.getByTeacherId();
    },
}
</script>

<style scoped>
.position{position: absolute;top: 0.84rem;left: 0;right: 0;bottom: 0;}
.permission{width: 100%;overflow: hidden;}
.perBox{height: 1rem;border-bottom: 1px solid #f2f2f2;}
.per-title,.look{line-height: 1rem;}
.per-img{width: 0.3rem;height: 0.3rem;display: block;}
.canvasBox{position: relative;}
.types{position: absolute;width: 0.6rem;height: 0.6rem;border-radius: 50%;text-align: center;line-height: 0.6rem;color: #fff;font-weight: bold;z-index: 99;}
.num1{left: 50%;margin-left: -0.3rem;top: 0;background: #ff732c;}
.num2{left: 0.8rem;top: 2rem;background: #f5a801;}
.num3{left: 2rem;bottom: 0.4rem;background: #00d4a9;}
.num4{right: 2rem;bottom: 0.4rem;background: #69acfa;}
.num5{right: 0.8rem;top: 2rem;background: #accf1b;}
.maskT{position: absolute;top: 0;left: 0;bottom: 0;right: 0;}
.tuli{overflow:hidden;width: 6.1rem;height: 2rem;display: block;margin:0 auto; background: linear-gradient(to right, #a281c7 , #8d88d4);border-radius: 7px;}
.biaozhu{width:33%;overflow: hidden;box-sizing: border-box;}
.qiu{width: 0.2rem;height: 0.2rem;border-radius: 50%;margin-top: 0.1rem}
.biaozhu1{width: 50%;}
.tuliP{color: #fff;font-weight: bold;}
.ff732c{background: #ff732c;}
.f5a801{background: #f5a801;}
.f00d4a9{background: #00d4a9;}
.f69acfa{background: #69acfa;}
.accf1b{background: #accf1b;}
.popups .position{position: absolute;top: 0.84rem;left: 0;right: 0;bottom: 0;overflow-y: auto;}
.popups .item-for{width: 100%;overflow: hidden;}
.popups .item-list{border-bottom: 1px solid #f2f2f2;line-height: 1rem;overflow: hidden;}
.popups .item-img{width: 0.3rem;height: 0.3rem;display: block;margin: 0.3rem 0.3rem 0 0;}
/* .popups .chooseClass{transition: all 1s;clear: both;height: 100%;overflow: hidden;}*/
.popups .chooseClass{clear: both;overflow: hidden;} 
.popups .chooseClass .item-list{color: #adadad;clear: both;}
.popups .line{width: 0.15rem;height: 0.02rem;background: #adadad;margin: 0.52rem 0.1rem 0 0 }
.popupModel .loginNavFix{z-index: 999999999 !important;}
</style>






